        tit:"09-26() - 3660/ - A",
        godata:"2019-09-26",
        ptime:false
      },{
        tit:"10-01() - 4460/ - A",
        godata:"2019-10-01",
        ptime:false
      },
      {
        tit:"10-02() - 4460/ - A",
        godata:"2019-10-02",
