<script>
export default {
  data(){
    return{
      i: 0,
      time: "",
      lilist:[" "," 300/"," 800/"," 1000/","",""]
    }
  },
  methods: {
    move(i) {
      this.i += i;
      if (this.i == 0 && i == -1) {
        this.i = this.lilist.length - 1;
      } else if (this.i == this.lilist.length && i == 1) {
        this.i = 0;
      }
    },
    moveTo(idx) {
      this.i = 0;
      this.move(idx);
    },
    Lea() {
      this.time = setInterval(() => {
        this.move(1);
      }, 3000);
    },
    Ent() {
      clearInterval(this.time);
    }
  }
}
</script>